.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  gap: 24px;
}

.score-container {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #0b1215;
  border-radius: 5px;
  width: 120px;
  height: 50px;
  background-color: #343233;
}

.score-count {
  font-family: 'DigitalNumbers', serif;
  color: #f4f1ea;
  font-size: 24px;
}

.canvas-container {
  position: relative;
  width: 628px;
  height: 628px;
  background: url('./assets//background.svg') no-repeat center center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}

canvas {
  background: transparent;
}

.lives-container {
  position: absolute;
  top: -30px;
  left: 5px;
  display: flex;
  align-items: center;
  font-size: 16px;
  font-family: 'DigitalNumbers', serif;
}

.edit-container {
  position: absolute;
  top: -30px;
  right: 5px;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 18px;
  cursor: pointer;
}

.message-container {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 10px;
  border-radius: 5px;
  font-size: 16px;
  z-index: 10;
}
