@font-face {
  font-family: 'DigitalNumbers';
  src: url('../public/fonts/DigitalNumbers-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Nunito';
  font-weight: 900;
  src: url('../public/fonts/Nunito-Black.ttf') format('truetype');
}

@font-face {
  font-family: 'Nunito';
  font-weight: 700;
  src: url('../public/fonts/Nunito-Bold.ttf') format('truetype');
}

@font-face {
  font-family: 'Nunito';
  font-weight: 400;
  src: url('../public/fonts/Nunito-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Nunito';
  font-weight: 600;
  src: url('../public/fonts/Nunito-SemiBold.ttf') format('truetype');
}

body {
  margin: 0;
  font-family: 'Nunito', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  background-color: #eeefe9;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}
