.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  width: 400px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  animation: fadeIn 0.3s ease-out;
}

.modal h2 {
  margin-top: 0;
  font-size: 24px;
  color: #333;
}

.setting-item {
  margin-top: 15px;
}

.setting-item label {
  font-size: 16px;
  color: #555;
  display: block;
  margin-bottom: 5px;
}

.setting-item input[type='number'] {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.setting-item.error input[type='number'] {
  border-color: red;
}

.error-message {
  color: red;
  font-size: 14px;
  margin-top: 5px;
}

.effects-list {
  margin-top: 20px;
}

.effects-list h3 {
  margin-bottom: 10px;
  font-size: 18px;
  color: #333;
}

.effect-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  font-size: 16px;
  color: #555;
}

.effect-item input[type='checkbox'] {
  margin-right: 10px;
  accent-color: #007bff;
}

.modal-actions {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}

.modal-actions button {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-left: 10px;
  transition: background 0.3s;
}

.modal-actions button:hover {
  background: #0056b3;
}

.modal-actions button:last-child {
  background: #6c757d;
}

.modal-actions button:last-child:hover {
  background: #5a6268;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
